<template>
    <div v-if="ready">
        <div class="col-12">
            <div class="card" style='height:auto'>
                <div class="card-header">
                    <strong>Export Settings</strong>
                </div>            
                <div class="card-body card-block">
                    <div class="col-12 px0 cprow" style='margin-bottom:0.5rem!important;'>
                        <label><b><u>Filter(s)</u></b></label>
                    </div>
                    <div class="col-12 px0 cprow flex">
                        <div class="col-4 px0">
                            <label>
                                Section
                            </label>
                        </div>
                        <div class="col-8 px0">
                            <select class="form-control select" v-model="section" @change="load_filter">
                                <option value="All">All</option>
                                <option value="S/">Secondary section</option>
                                <option value="P/">Primary section</option>
                                <option value="K/">Kindergarten section</option>
                            </select>
                        </div>
                    </div>
                    <div class="col-12 px0 cprow flex">
                        <div class="col-4 px0">
                            <label>
                                Group
                            </label>
                        </div>
                        <div class="col-8 px0">
                            <select class="form-control select" v-model="school">
                                <option value="All">All</option>
                                <option  v-for="g in grp_list.length" :value="grp_list[g-1].grp_code">{{ grp_list[g-1].group_description }}</option>
                            </select>
                        </div>
                    </div>
                    <div class="col-12 px0 cprow flex">
                        <div class="col-4 px0">
                            <label>
                                Block
                            </label>
                        </div>
                        <div class="col-8 px0">
                            <select class="form-control select" v-model="blk">
                                <option value="All">All Blocks</option>
                                <option value="WHC">Whole Campus</option>                               
                                <option v-for="n in 8" :value="blks[n-1]">Block {{ blks[n-1] }}</option>
                                <option value="SP">Special</option>
                                <option value="OC">Common Area</option>
                            </select>
                        </div>
                    </div>
                    <div class="col-12 px0 cprow flex">
                        <div class="col-4 px0">
                            <label>
                                Floor
                            </label>
                        </div>
                        <div class="col-8 px0">
                            <select class="form-control select" v-model="flr">
                                <option value="All">All</option>
                                <option value="WHC">Whole Campus</option>
                                <option value="G">Ground floor</option>
                                <option v-for="f in 8" :value="f"> {{ f }}<sup>{{ index_sup[f-1] }}</sup> floor</option>
                                <option value="SP">Special</option>
                            </select>
                        </div>
                    </div>
                    <div class="col-12 px0 cprow flex">
                        <div class="col-4 px0">
                            <label>
                                Category
                            </label>
                        </div>
                        <div class="col-8 px0">
                            <select class="form-control select" v-model="category">
                                <option value="All">All</option>
                                <option v-for="l in category_list.length" :value="category_list[l-1].main_category">{{ category_list[l-1].main_cat_name }}</option>
                            </select>
                        </div>
                    </div>
                    <div class="col-12 px0 cprow flex">
                        <div class="col-4 px0">
                            <label>
                                Sub-Category
                            </label>
                        </div>
                        <div class="col-8 px0">
                            <select class="form-control select" v-model="sub_category">
                                <option value="All">All</option>
                                <option v-for="l in sub_category_list.length" :value="sub_category_list[l-1].sub_category">{{ sub_category_list[l-1].sub_cat_name }}</option>
                            </select>
                        </div>
                    </div>
                    <div class="col-12 px0 cprow flex">
                        <div class="col-4 px0">
                            <label>
                                Type
                            </label>
                        </div>
                        <div class="col-8 px0">
                            <select class="form-control select" v-model="scope">
                                <option value="single">Single Item</option>
                                <option value="bulk">Bulk Item</option>
                            </select>
                        </div>
                    </div>
                </div>
                <div class="card-footer">
                    <button type="button" class="btn btn-primary btn-sm" @click="export_file">
                        <i class="fa fa-file-excel-o"></i> Export
                    </button>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    data(){
        return{
            ready:false,
            data:[],
            headers: [
                { text: 'Details',value :'item_code',sortable: false},
                { text: 'Item Code',value :'item_code',sortable: true},
                { text: 'Barcode',value:'barcode',sortable: true},
                { text: 'Name',value:'eng_name',sortable: true},                
                { text: 'Location',value:'location',sortable: true},
            ],
            section:'All',
            grp_list:[],
            school:'All',
            room_list:[],
            room:'All',
            category_list:[],
            category:'All',
            sub_category_list:[],
            sub_category:'All',
            scope:'single',
            blk:'All',
            blks:['A','B','C','D','E','F','G','H'],
            flr:'All',
            index_sup:['st','nd','rd','th','th','th','th','th'],
            type:'All',
            key:'',
        }
    },
    async created(){
        await this.load_filter();
        this.ready = true;
    },
    methods:{
        new_record(){
            this.$router.push('insert_item');
        },
        async export_file(){
            var formData = new FormData();
            formData.append('section',this.section);
            formData.append('blk',this.blk);
            formData.append('floor',this.flr);
            formData.append('school',this.school);
            formData.append('scope',this.scope);
            formData.append('key',this.key);
            formData.append('category',this.category);
            formData.append('sub_category',this.sub_category);
            await axios.post('inventory/web/backend/export_list.php',formData)
                .then(
                    (response)=>{
                        const url = window.URL.createObjectURL(new Blob([response.data]));
                        const link = document.createElement('a');
                        link.href = url;
                        link.setAttribute('download', 'Inventory_record_'+this.blk+'_'+this.flr+'_'+this.category+'_'+this.sub_category+'.csv'); //or any other extension
                        document.body.appendChild(link);
                        link.click();
                    }
                )
        },
        async load_filter(){
            var formData = new FormData();
            formData.append('section',this.section);
            await axios.post('inventory/web/backend/filter_preload.php',formData)
                .then(
                    (response)=>{
                        if(response.data!='empty'){
                            this.category_list = response.data.category;
                            this.grp_list = response.data.grp_code;
                        }
                    }
                )
        },
        async view_details(id){
            document.getElementById('tmp_id').value = id;
            document.getElementById('tmp_id_2').value = this.scope;
            this.$router.push('edit_item');
        },
        async fetch_sub_category(){
            var formData = new FormData();
            formData.append('category',this.category);
            await axios.post('inventory/web/backend/fetch_sub_category.php',formData)
                .then(
                    (response)=>{
                        if(response.data!='empty'){
                            this.sub_category_list = response.data;
                            this.sub_category = 'All';
                        } else {
                            this.sub_category_list = [];
                            this.sub_category = 'All';
                        }
                    }
                )
        },
        async initialize(){
            this.category = 'All';
            this.blk = 'All';
            this.flr = 'All';
            this.school = 'All';
            this.key = '';
        },
        async reset(){
            await this.initialize();
        }
    }
}
</script>
<style scoped>
.flex{
    display:flex;
    flex-direction:row;
}
.px0{
    padding-left:0;
    padding-right:0;
}
.click_row{
    cursor: pointer;
}
u {
    color:rgba(0,0,0,0.6);
}
.cprow{
    margin-bottom:1rem;
}
td{
    text-transform: capitalize!important;
}
label{
    padding-top:0.35rem!important;
}
</style>


